body {
    margin: 0 0 50 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
        'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: rgb(237, 237, 237);
    font-size: 20px;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}
.is-hidden{
    display: none;
}
.search-form{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;
    height: 80px;
    background: linear-gradient(to top right, #ffffff -57%, #000066 58%);
}
.search-form__input{
    width: 200px;
    padding: 10px;
    height: 20px;
    margin-right: 50px;
    border-radius: 50px;
    border: 1px solid #000066;
    font-size: 15px;
    transition: width cubic-bezier(0.075, 0.82, 0.165, 1) 250ms,
                height cubic-bezier(0.075, 0.82, 0.165, 1) 250ms;
    &:focus{
        width: 250px;
        height: 25px;
        padding: 20px;
    }
}
button{
    padding: 10px 50px;
    background-color: azure;
    border-radius: 10px;
    font-size: 15px;
    transition: background-color cubic-bezier(0.075, 0.82, 0.165, 1) 250ms,
    color cubic-bezier(0.075, 0.82, 0.165, 1) 250ms,
    padding cubic-bezier(0.075, 0.82, 0.165, 1) 250ms;
    cursor: pointer;
    border: none;
    &:hover,
    &:focus{
        background-color: rgb(202, 202, 202);
        color: white;
        padding: 20px 60px;
            border: 2px solid #3d2511;
    }
}
img{
    width: 300px;
    height: 200px;
}
.photo-card{
    display: inline-block;
    margin-left: 30px;
}
.cards{
    display: flex;
    flex-direction: column;
    margin-left: 30px;
    margin-top: 50px;
    // flex-direction: column;
    // flex-wrap: wrap;
    // // justify-content: center;
    // // align-items: center;
    // margin-left: 100px;
}
.info{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    font-size: 15px;
    padding: 20px;
    width: 260px;
    background-color: azure;
    border-radius: 10px;
    margin-top: -10px;
    z-index: -1;
    box-shadow: 0 19px 20px 4px rgba(63, 63, 63, 0.9);
// box-shadow: -2px 19px 44px -4px rgba(0, 0, 0, 0.75);
    &-item{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}

.load-more{
    display: block;
